import 'date-fns';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {Button, Divider} from "@material-ui/core";
import {PropTypes} from "prop-types";
import Login from "./Login";

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
}));

function DrugReturnView(props) {
    const classes = useStyles();
    const [medicalRecordId, setMedicalRecordId] = React.useState(null);
    const [drugId, setDrugId] = React.useState(null);
    const [drugNum, setDrugNum] = React.useState(null);

    return (
        <div>
            <React.Fragment>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Divider/>
                <Grid container>
                    <Grid item xs={6} sm={6} className={classes.formControl}>
                        <TextField required id="medical_record_id" label=""
                                   value={medicalRecordId}
                                   onChange={event => setMedicalRecordId(event.target.value)}/>
                    </Grid>
                    <Grid item xs={6} sm={6} className={classes.formControl}>
                        <TextField required id="drugId" label="ID"
                                   value={drugId}
                                   onChange={event => setDrugId(event.target.value)}/>
                    </Grid>
                    <Grid item xs={6} sm={6} className={classes.formControl}>
                        <TextField required id="drugNum" label=""
                                   value={drugNum}
                                   onChange={event => setDrugNum(event.target.value)}/>
                    </Grid>
                </Grid>
            </React.Fragment>
            <Divider style={{marginTop: "0.5em"}}/>
            <Button variant="contained" color="primary" style={{marginTop: "1em"}} disableElevation
                    onClick={() => props.onClick(medicalRecordId, drugId, drugNum)}>
                
            </Button>
        </div>
    )
}

export default class DrugReturn extends React.Component {

    render() {
        return (
            <DrugReturnView
                onClick={(medicalRecordId, drugId, drugNum) => {
                    let data = new FormData();
                    data.append("medical_record_id", medicalRecordId);
                    data.append("drug_id", drugId);
                    data.append("drug_num", drugNum)

                    fetch("/finance/drug/return", {
                        method: "POST",
                        body: data
                    })
                        .then(res => res.json())
                        .then(res => {
                                alert(res.msg)
                            }, error => {
                                alert("")
                            }
                        )
                }}
            />
        );
    }
}

DrugReturn.contextType = {
    router: PropTypes.object.isRequired
}