import React from "react";
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import {makeStyles} from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Cookies from 'js-cookie'
import {PropTypes} from "prop-types";

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));

function LoginView(props) {
    const classes = useStyles();
    const [staffId, setStaffId] = React.useState(null);
    const [password, setPassword] = React.useState(null);
    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline/>
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon/>
                </Avatar>
                <Typography component="h1" variant="h5">
                    
                </Typography>
                <form className={classes.form} noValidate onSubmit={event => event.preventDefault()}>
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        id="staff_id"
                        label=""
                        value={staffId}
                        onChange={event => setStaffId(event.target.value)}
                        name="staff_id"
                        autoComplete="staff_id"
                        autoFocus
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        name="password"
                        label=""
                        value={password}
                        onChange={event => setPassword(event.target.value)}
                        type="password"
                        id="password"
                        autoComplete="current-password"
                    />
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                        onClick={() => props.onClick(staffId, password)}
                    >
                        
                    </Button>
                </form>
            </div>
        </Container>
    )
}

export default class Login extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        return <LoginView
            onClick={(staff_id, password) => {
                let data = new FormData();
                data.append("staff_id", staff_id);
                data.append("password", password);

                fetch("/login", {
                    method: "POST",
                    body: data
                })
                    .then(res => res.json())
                    .then(res => {
                        if (res.code === 0) {
                            Cookies.set("staff_auth", res.data.auth)
                            Cookies.set("staff_id", staff_id)
                            Cookies.set("department", res.data.department)
                            this.props.router.push({
                                pathname: "/main"
                            })
                        } else {
                            alert(res.msg)
                        }
                    }, error => {
                        alert("")
                    })
            }}
        />
    }
}

Login.contextType = {
    router: PropTypes.object.isRequired
}