import 'date-fns';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {Button, Divider, Paper} from "@material-ui/core";
import TableContainer from "@material-ui/core/TableContainer";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {PropTypes} from "prop-types";
import DrugStock from "./DrugStock";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Dialog from "@material-ui/core/Dialog";

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
    table: {
        minWidth: 650,
    },
}));

function createData(equipmentId, name, location, value) {
    return {equipmentId, name, location, value};
}

function EquipmentStockView(props) {
    const classes = useStyles();
    const [equipmentName, setEquipmentName] = React.useState(null);
    const [open, setOpen] = React.useState(false);
    const [equipmentLocation, setEquipmentLocation] = React.useState(null);
    const [equipmentValue, setEquipmentValue] = React.useState(null);
    const [equipmentNameSelect, setEquipmentNameSelect] = React.useState(null);
    const rows = props.data;

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <React.Fragment>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Divider/>
                <Grid container className={classes.div_inline}>
                    <Grid className={classes.formControl}>
                        <TextField required id="equipment_name" label=""
                                   value={equipmentNameSelect}
                                   onChange={event => setEquipmentNameSelect(event.target.value)}/>
                    </Grid>
                </Grid>
                <Button variant="contained" color="primary" style={{marginTop: "0.5em"}} disableElevation
                        onClick={() => props.onSelect(equipmentNameSelect)}>
                    
                </Button>
                <Button variant="contained" style={{
                    marginLeft: "0.5em",
                    marginTop: "0.5em",
                    broaderColor: "green",
                    backgroundColor: "green",
                    color: "white"
                }} disableElevation
                        onClick={handleClickOpen}>
                    
                </Button>
                <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title"></DialogTitle>
                    <DialogContent>
                        <TextField
                            autoFocus
                            margin="dense"
                            id="equipment_name"
                            value={equipmentName}
                            onChange={event => setEquipmentName(event.target.value)}
                            label=""
                            fullWidth
                        />
                        <TextField
                            autoFocus
                            margin="dense"
                            id="equipment_location"
                            value={equipmentLocation}
                            onChange={event => setEquipmentLocation(event.target.value)}
                            label=""
                            fullWidth
                        />
                        <TextField
                            autoFocus
                            margin="dense"
                            id="equipment_value"
                            value={equipmentValue}
                            onChange={event => setEquipmentValue(event.target.value)}
                            label=""
                            fullWidth
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleClose} color="primary">
                            
                        </Button>
                        <Button
                            onClick={() => props.onAdd(equipmentName, equipmentLocation, parseInt(equipmentValue * 100))}
                            color="primary">
                            
                        </Button>
                    </DialogActions>
                </Dialog>
            </React.Fragment>
            <Divider style={{marginTop: "1em"}}/>
            {/**/}
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>ID</TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell/>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            row.name.indexOf(props.keyword) !== -1 ?
                                <TableRow key={row.equipmentId}>
                                    <TableCell component="th" scope="row">
                                        {row.equipmentId}
                                    </TableCell>
                                    <TableCell>{row.name}</TableCell>
                                    <TableCell>{row.location}</TableCell>
                                    <TableCell>{row.value}</TableCell>
                                    <TableCell align={"right"}>
                                        <Button variant="contained" style={{
                                            marginLeft: "1em",
                                            backgroundColor: "red",
                                            borderColor: "red",
                                            color: "white"
                                        }} disableElevation
                                                onClick={() => props.onDelete(row.equipmentId)}></Button>
                                    </TableCell>
                                </TableRow> : null
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    )
}

export default class EquipmentStock extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            keyword: ""
        }
    }

    componentDidMount() {
        fetch("/info/equipment?equipment_name=", {
            method: "GET",
        })
            .then(res => res.json())
            .then(res => {
                if (res.code === 0) {
                    let data = JSON.parse(res.data.equipment_info)
                    let newData = [];
                    for (let json of data) {
                        newData.push(createData(json.equipment_id, json.equipment_name, json.equipment_location, json.value / 100))
                    }
                    this.setState({
                        data: newData
                    });
                } else {
                    alert(res.msg)
                }
            }, error => {
                alert("")
            })
    }

    render() {
        return <EquipmentStockView
            onSelect={(equipmentName) => {
                this.setState({
                    keyword: equipmentName
                })
            }}
            onAdd={(equipmentName, equipmentLocation, equipmentValue) => {
                let data = new FormData();
                data.append("equipment_name", equipmentName);
                data.append("equipment_location", equipmentLocation);
                data.append("value", equipmentValue);
                fetch("/finance/equipment/add", {
                    method: "POST",
                    body: data
                })
                    .then(res => res.json())
                    .then(res => {
                        alert(res.msg)
                        if (res.code === 0) {
                            window.location.reload()
                        }
                    }, error => {
                        alert("")
                    })
            }}
            onDelete={(equipmentId) => {
                let data = new FormData();
                data.append("equipment_id", equipmentId);
                fetch("/finance/equipment/remove", {
                    method: "POST",
                    body: data
                })
                    .then(res => res.json())
                    .then(res => {
                        alert(res.msg)
                        if (res.code === 0) {
                            window.location.reload()
                        }
                    }, error => {
                        alert("")
                    })
            }}
            data={this.state.data}
            keyword={this.state.keyword}
        />
    }
}

EquipmentStock.contextType = {
    router: PropTypes.object.isRequired
}