import 'date-fns';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {Button, Divider} from "@material-ui/core";
import InputAdornment from "@material-ui/core/InputAdornment";
import {PropTypes} from "prop-types";

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
}));

function RechargeView(props) {
    const classes = useStyles();
    const [balance, setBalance] = React.useState(null);
    const [medicalRecordId, setMedicalRecordId] = React.useState(null);

    return (
        <div>
            <React.Fragment>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Divider/>
                <Grid container>
                    <Grid item xs={6} sm={6} className={classes.formControl}>
                        <TextField required id="medical_record_id" label=""
                                   value={medicalRecordId}
                                   onChange={event => setMedicalRecordId(event.target.value)}/>
                    </Grid>
                </Grid>
            </React.Fragment>
            <div>
                <TextField required id="balance" label="" className={classes.formControl}
                           InputProps={{
                               endAdornment: <InputAdornment position="end"></InputAdornment>,
                           }}
                           value={balance}
                           onChange={event => setBalance(event.target.value)}/>
            </div>
            <Divider style={{marginTop: "0.5em"}}/>
            <Button variant="contained" color="primary" style={{marginTop: "1em"}} disableElevation
                    onClick={() => props.onClick(medicalRecordId, parseInt(balance * 100))
                    }>
            </Button>
        </div>
    )
}

export default class Recharge extends React.Component {
    render() {
        return <RechargeView
            onClick={(medicalRecordId, balance) => {
                if (balance < 0 || balance >= 1000000) {
                    alert("")
                } else {
                    let data = new FormData();
                    data.append("medical_record_id", medicalRecordId);
                    data.append("amount", balance);
                    fetch("/finance/recharge", {
                        method: "POST",
                        body: data
                    })
                        .then(res => res.json())
                        .then(res => {
                            alert(res.msg)
                        }, error => {
                            alert("")
                        })
                }
            }}
        />
    }
}

Recharge.contextType = {
    router: PropTypes.object.isRequired
}