import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Login from './pages/Login';
import Main from './pages/Main';
import Admin from './pages/Admin'
import Admission from './pages/Admission'
import NewAdmission from "./pages/NewAdmission";
import Discharge from "./pages/Discharge";
import Transfer from "./pages/Transfer";
import PatientInfo from "./pages/PatientInfo";
import Finance from "./pages/Finance";
import Recharge from "./pages/Recharge";
import RecoursesSchedule from "./pages/RecoursesSchedule";
import Stock from "./pages/Stock"
import DrugStock from "./pages/DrugStock";
import EquipmentStock from "./pages/EquipmentStock";
import Order from "./pages/Order"

import {Route, Router, hashHistory, Redirect} from 'react-router'
import DrugOrder from "./pages/DrugOrder";
import EquipmentOrder from "./pages/EquipmentOrder";
import DrugReturn from "./pages/DrugReturn";
import SickbedStock from "./pages/SickbedStock";
import StaffStock from "./pages/StaffStock";

ReactDOM.render((
    <Router history={hashHistory}>
        <Route path="/" component={Login} exact/>
        <Route path="/main" component={Main}>
            <Route path="admin" component={Admin}>
                <Route path="new_admission" component={NewAdmission}/>
                <Route path="admission" component={Admission}/>
                <Route path="discharge" component={Discharge}/>
                <Route path="transfer" component={Transfer}/>
                <Route path="patient_info" component={PatientInfo}/>
            </Route>
            <Route path="finance" component={Finance}>
                <Route path="recharge" component={Recharge}/>
                <Route path="recourses_schedule" component={RecoursesSchedule}/>
                <Route path="drug_return" component={DrugReturn}/>
            </Route>
            <Route path="stock" component={Stock}>
                <Route path="drug" component={DrugStock}/>
                <Route path="equipment" component={EquipmentStock}/>
                <Route path="sickbed" component={SickbedStock}/>
                <Route path="staff" component={StaffStock}/>
            </Route>
            <Route path="order" component={Order}>
                <Route path="drug" component={DrugOrder}/>
                <Route path="equipment" component={EquipmentOrder}/>
            </Route>
        </Route>
        <Redirect from="/*" to="/main"/>
    </Router>
), document.getElementById('root'));