import 'date-fns';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {Button, Divider, Paper} from "@material-ui/core";
import TableContainer from "@material-ui/core/TableContainer";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {PropTypes} from "prop-types";

import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
    table: {
        minWidth: 650,
    },
}));

function createData(bedId, roomId, bedDepartment, bedPrice, bedStatus) {
    return {bedId, roomId, bedDepartment, bedPrice, bedStatus};
}

function DrugStockView(props) {
    const classes = useStyles();
    const rows = props.data;
    const [open, setOpen] = React.useState(false);
    const [roomId, setRoomId] = React.useState(null);
    const [bedDepartment, setBedDepartment] = React.useState(null);
    const [bedPrice, setBedPrice] = React.useState(null);
    const [department, setDepartment] = React.useState(null);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <React.Fragment>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Divider/>
                <Grid container className={classes.div_inline}>
                    <Grid className={classes.formControl}>
                        <TextField required id="department" label=""
                                   value={department}
                                   onChange={event => setDepartment(event.target.value)}/>
                    </Grid>
                </Grid>
                <Button variant="contained" color="primary" style={{marginTop: "0.5em"}} disableElevation
                        onClick={() => props.onClickSelect(department)}>
                    
                </Button>
                <Button variant="contained" style={{
                    marginLeft: "0.5em",
                    marginTop: "0.5em",
                    broaderColor: "green",
                    backgroundColor: "green",
                    color: "white"
                }}
                        onClick={handleClickOpen}
                        disableElevation>
                    
                </Button>

                {/**/}
                <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title"></DialogTitle>
                    <DialogContent>
                        <TextField
                            autoFocus
                            margin="dense"
                            id="roomId"
                            value={roomId}
                            onChange={event => setRoomId(event.target.value)}
                            label="ID"
                            fullWidth
                        />
                        <FormControl className={classes.formControl}>
                            <InputLabel id="bed_department"></InputLabel>
                            <Select
                                id="bed_department"
                                value={bedDepartment}
                                onChange={event => setBedDepartment(event.target.value)}
                            >
                                <MenuItem value={""}></MenuItem>
                                <MenuItem value={""}></MenuItem>
                            </Select>
                        </FormControl>
                        <TextField
                            autoFocus
                            margin="dense"
                            id="price"
                            value={bedPrice}
                            onChange={event => setBedPrice(event.target.value)}
                            label=""
                            fullWidth
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleClose} color="primary">
                            
                        </Button>
                        <Button onClick={() => props.onClickAdd(roomId, bedDepartment, parseInt(bedPrice * 100))}
                                color="primary">
                            
                        </Button>
                    </DialogActions>
                </Dialog>

            </React.Fragment>
            <Divider style={{marginTop: "1em"}}/>
            {/**/}
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>ID</TableCell>
                            <TableCell>ID</TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell/>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            row.bedDepartment.indexOf(props.keyword) !== -1 ?
                                <TableRow key={row.bedId}>
                                    <TableCell component="th" scope="row">
                                        {row.bedId}
                                    </TableCell>
                                    <TableCell>{row.roomId}</TableCell>
                                    <TableCell>{row.bedDepartment}</TableCell>
                                    <TableCell>{row.bedPrice}</TableCell>
                                    <TableCell>{row.bedStatus ? "" : ""}</TableCell>
                                    <TableCell align={"right"}>
                                        {<Button variant="contained" style={row.bedStatus ? {
                                            marginLeft: "1em",
                                            backgroundColor: "red",
                                            borderColor: "red",
                                            color: "white",
                                            visibility: "hidden"
                                        } : {
                                            marginLeft: "1em",
                                            backgroundColor: "red",
                                            borderColor: "red",
                                            color: "white"
                                        }}
                                                 onClick={() => props.onClickDelete(row.bedId)}
                                                 disableElevation></Button>}
                                    </TableCell>
                                </TableRow> : null
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    )
}

export default class SickbedStock extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            keyword: ""
        }
    }

    componentDidMount() {
        fetch("/info/sickbed?department=" + "", {
            method: "GET",
        })
            .then(res => res.json())
            .then(res => {
                if (res.code === 0) {
                    let data = JSON.parse(res.data.sickbed_info)
                    let newData = [];
                    for (let json of data) {
                        newData.push(createData(json.bed_id, json.room_id, json.bed_department, json.bed_price / 100, json.bed_status));
                    }
                    this.setState({
                        data: newData
                    });
                } else {
                    alert(res.msg)
                }
            }, error => {
                alert("")
            })
    }

    render() {
        return <DrugStockView
            onClickSelect={(department) => {
                this.setState({
                    keyword: department
                })
            }}
            onClickAdd={(roomId, bedDepartment, bedPrice) => {
                let data = new FormData();
                data.append("room_id", roomId);
                data.append("bed_department", bedDepartment);
                data.append("bed_price", bedPrice);

                fetch("/finance/sickbed/add", {
                    method: "POST",
                    body: data
                })
                    .then(res => res.json())
                    .then(res => {
                        alert(res.msg)
                        if(res.code === 0){
                            window.location.reload();
                        }
                    }, error => {
                        alert("")
                    })
            }}
            onClickDelete={(bedId) => {
                let data = new FormData();
                data.append("bed_id", bedId);

                fetch("/finance/sickbed/remove", {
                    method: "POST",
                    body: data
                })
                    .then(res => res.json())
                    .then(res => {
                        alert(res.msg)
                        if(res.code === 0){
                            window.location.reload()
                        }
                    }, error => {
                        alert("")
                    })
            }}
            data={this.state.data}
            keyword={this.state.keyword}
        />
    }
}

SickbedStock.contextType = {
    router: PropTypes.object.isRequired
}
