import 'date-fns';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {Button, Divider, Paper} from "@material-ui/core";
import TableContainer from "@material-ui/core/TableContainer";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {PropTypes} from "prop-types";
import Cookies from 'js-cookie'
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Dialog from "@material-ui/core/Dialog";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
    table: {
        minWidth: 650,
    },
}));

function createData(staffId, staffAuth, staffName, staffSex, staffDepartment) {
    if (staffAuth === 1) {
        staffAuth = ""
    } else if (staffAuth === 0) {
        staffAuth = ""
    } else if (staffAuth === 2) {
        staffAuth = ""
    }
    return {staffId, staffAuth, staffName, staffSex, staffDepartment};
}

function StaffStockView(props) {
    const classes = useStyles();
    const [staffNameSelect, setStaffNameSelect] = React.useState(null);
    const [open, setOpen] = React.useState(false);
    const [staffId, setStaffId] = React.useState(null);
    const [staffAuth, setStaffAuth] = React.useState(null);
    const [staffName, setStaffName] = React.useState(null);
    const [staffSex, setStaffSex] = React.useState(null);
    const [staffDepartment, setStaffDepartment] = React.useState(null);
    const [password, setPassword] = React.useState(null);
    const rows = props.data;

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <React.Fragment>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Divider/>
                <Grid container className={classes.div_inline}>
                    <Grid className={classes.formControl}>
                        <TextField required id="staff_name_select" label=""
                                   value={staffNameSelect}
                                   onChange={event => setStaffNameSelect(event.target.value)}/>
                    </Grid>
                </Grid>
                <Button variant="contained" color="primary" style={{marginTop: "0.5em"}} disableElevation
                        onClick={() => props.onSelect(staffNameSelect)}>
                    
                </Button>
                <Button variant="contained" style={{
                    marginLeft: "0.5em",
                    marginTop: "0.5em",
                    broaderColor: "green",
                    backgroundColor: "green",
                    color: "white"
                }} disableElevation
                        onClick={handleClickOpen}>
                    
                </Button>
                <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title"></DialogTitle>
                    <DialogContent>
                        <TextField
                            autoFocus
                            margin="dense"
                            id="staff_name"
                            value={staffName}
                            onChange={event => setStaffName(event.target.value)}
                            label=""
                            fullWidth
                        />
                        <FormControl className={classes.formControl}>
                            <InputLabel id="staff_sex"></InputLabel>
                            <Select
                                id="staff_sex"
                                value={staffSex}
                                onChange={event => setStaffSex(event.target.value)}
                            >
                                <MenuItem value={""}></MenuItem>
                                <MenuItem value={""}></MenuItem>
                            </Select>
                        </FormControl>
                        <FormControl className={classes.formControl}>
                            <InputLabel id="staff_auth"></InputLabel>
                            <Select
                                id="staff_auth"
                                value={staffAuth}
                                onChange={event => setStaffAuth(event.target.value)}
                            >
                                <MenuItem value={1}></MenuItem>
                                <MenuItem value={0}></MenuItem>
                                <MenuItem value={2}></MenuItem>
                            </Select>
                        </FormControl>
                        <FormControl className={classes.formControl}>
                            <InputLabel id="staff_department"></InputLabel>
                            <Select
                                id="staff_department"
                                value={staffDepartment}
                                onChange={event => setStaffDepartment(event.target.value)}
                            >
                                <MenuItem value={""}></MenuItem>
                                <MenuItem value={""}></MenuItem>
                                <MenuItem value={""}></MenuItem>
                            </Select>
                        </FormControl>
                        <TextField
                            autoFocus
                            margin="dense"
                            id="password"
                            value={password}
                            onChange={event => setPassword(event.target.value)}
                            label=""
                            fullWidth
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleClose} color="primary">
                            
                        </Button>
                        <Button
                            onClick={() => props.onAdd(staffName, staffSex, staffAuth, staffDepartment, password)}
                            color="primary">
                            
                        </Button>
                    </DialogActions>
                </Dialog>
            </React.Fragment>
            <Divider style={{marginTop: "1em"}}/>
            {/**/}
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>ID</TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell/>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            row.staffName.indexOf(props.keyword) !== -1 ?
                                <TableRow key={row.staffId}>
                                    <TableCell component="th" scope="row">
                                        {row.staffId}
                                    </TableCell>
                                    <TableCell>{row.staffName}</TableCell>
                                    <TableCell>{row.staffSex}</TableCell>
                                    <TableCell>{row.staffAuth}</TableCell>
                                    <TableCell>{row.staffDepartment}</TableCell>
                                    <TableCell align={"right"}>
                                        <Button variant="contained" style={{
                                            marginLeft: "1em",
                                            backgroundColor: "red",
                                            borderColor: "red",
                                            color: "white"
                                        }} disableElevation
                                                onClick={() => props.onDelete(row.staffId)}></Button>
                                    </TableCell>
                                </TableRow> : null
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    )
}

export default class StaffStock extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            keyword: ""
        }
    }

    componentDidMount() {
        fetch("/info/staff?staff_name=", {
            method: "GET",
        })
            .then(res => res.json())
            .then(res => {
                if (res.code === 0) {
                    let data = JSON.parse(res.data.staff_info)
                    let newData = [];
                    for (let json of data) {
                        newData.push(createData(json.staff_id, json.staff_auth, json.staff_name, json.staff_sex, json.staff_department))
                    }
                    this.setState({
                        data: newData
                    });
                } else {
                    alert(res.msg)
                }
            }, error => {
                alert("")
            })
    }

    render() {
        return <StaffStockView
            onSelect={(staffName) => {
                this.setState({
                    keyword: staffName
                })
            }}
            onAdd={(staffName, staffSex, staffAuth, staffDepartment, password) => {
                let data = new FormData();
                data.append("staff_name", staffName);
                data.append("staff_sex", staffSex);
                data.append("staff_auth", staffAuth);
                data.append("staff_department", staffDepartment);
                data.append("password", password);
                fetch("/finance/staff/add", {
                    method: "POST",
                    body: data
                })
                    .then(res => res.json())
                    .then(res => {
                        alert(res.msg)
                        if (res.code === 0) {
                            window.location.reload()
                        }
                    }, error => {
                        alert("")
                    })
            }}
            onDelete={(staffId) => {
                if (parseInt(Cookies.get("staff_id")) === parseInt(staffId)) {
                    alert("")
                } else {
                    let data = new FormData();
                    data.append("staff_id", staffId);
                    fetch("/finance/staff/remove", {
                        method: "POST",
                        body: data
                    })
                        .then(res => res.json())
                        .then(res => {
                            alert(res.msg)
                            if (res.code === 0) {
                                window.location.reload()
                            }
                        }, error => {
                            alert("")
                        })
                }
            }}
            data={this.state.data}
            keyword={this.state.keyword}
        />
    }
}

StaffStock.contextType = {
    router: PropTypes.object.isRequired
}