import 'date-fns';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import Autocomplete from '@material-ui/lab/Autocomplete';
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import makeStyles from "@material-ui/core/styles/makeStyles";
import CircularProgress from "@material-ui/core/CircularProgress";
import {Button, Divider} from "@material-ui/core";
import {PropTypes} from "prop-types";

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
}));

function TransferView(props) {
    const classes = useStyles();
    const [medicalRecordId, setMedicalRecordId] = React.useState(null);
    const [department, setDepartment] = React.useState(null);
    const [targetRoomId, setTargetRoomId] = React.useState(null);
    const [open, setOpen] = React.useState(false);
    const [options, setOptions] = React.useState([]);
    const loading = open && options.length === 0;

    React.useEffect(() => {
        let active = true;
        if (!loading) {
            return undefined;
        }

        (async () => {
            const response = await fetch('/info/room?department=' + department);

            const rooms = await response.json();
            const roomList = JSON.parse(rooms.data.room_list);

            if (active) {
                setOptions(Object.keys(roomList).map((key) => String(roomList[key])));
            }
        })();

        return () => {
            active = false;
        };
    }, [loading]);

    React.useEffect(() => {
        if (!open) {
            setOptions([]);
        }
    }, [open]);

    return (
        <div>
            <React.Fragment>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Divider/>
                <Grid container>
                    <Grid item xs={6} sm={6} className={classes.formControl}>
                        <TextField required id="medical_record_id" label=""
                                   value={medicalRecordId}
                                   onChange={event => setMedicalRecordId(event.target.value)}/>
                    </Grid>
                </Grid>
            </React.Fragment>
            <Grid container>
                <FormControl className={classes.formControl}>
                    <InputLabel id="department"></InputLabel>
                    <Select
                        id="department_selected"
                        value={department}
                        onChange={event => {
                            setTargetRoomId(null)
                            setDepartment(event.target.value)
                        }}
                    >
                        <MenuItem value={""}></MenuItem>
                        <MenuItem value={""}></MenuItem>
                    </Select>
                </FormControl>
                <Autocomplete
                    className={classes.formControl}
                    id="room"
                    style={{width: 300}}
                    onChange={(event, newValue) => setTargetRoomId(newValue)}
                    open={open}
                    value={targetRoomId}
                    onOpen={() => {
                        setOpen(true);
                    }}
                    onClose={() => {
                        setOpen(false);
                    }}
                    getOptionSelected={(option, value) => option.name === value.name}
                    options={options}
                    loading={loading}
                    renderInput={(params) => (
                        <TextField
                            {...params}
                            label=""
                            InputProps={{
                                ...params.InputProps,
                                endAdornment: (
                                    <React.Fragment>
                                        {loading ? <CircularProgress color="inherit" size={20}/> : null}
                                        {params.InputProps.endAdornment}
                                    </React.Fragment>
                                ),
                            }}
                        />
                    )}
                />
            </Grid>
            <Divider style={{marginTop: "0.5em"}}/>
            <Button variant="contained" color="primary" style={{marginTop: "1em"}} disableElevation
                    onClick={() => props.onClick(medicalRecordId, department, targetRoomId)}>
                
            </Button>
        </div>
    )
}

export default class Transfer extends React.Component {
    render() {
        return (
            <TransferView
                onClick={(medicalRecordId, department, targetRoomId) => {
                    let data = new FormData();
                    data.append("medical_record_id", medicalRecordId);
                    data.append("department", department);
                    data.append("target_room_id", targetRoomId);
                    fetch("/admin/transfer", {
                        method: "POST",
                        body: data
                    })
                        .then(res => res.json())
                        .then(res => {
                            alert(res.msg)
                        }, error => {
                            alert("")
                        })
                }}
            />
        );
    }
}

Transfer.contextType = {
    router: PropTypes.object.isRequired
}
