import React from "react";
import {makeStyles} from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import CssBaseline from "@material-ui/core/CssBaseline";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import List from "@material-ui/core/List";
import Typography from "@material-ui/core/Typography";
import Divider from "@material-ui/core/Divider";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import {Button} from "@material-ui/core";
import Cookies from 'js-cookie'
import {PropTypes} from "prop-types";

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
    root: {
        display: "flex"
    },
    appBar: {
        width: `calc(100% - ${drawerWidth}px)`,
        marginLeft: drawerWidth
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0
    },
    drawerPaper: {
        width: drawerWidth
    },
    // necessary for content to be below app bar
    toolbar: theme.mixins.toolbar,
    actionBar: {
        display: "flex",
        justifyContent: "space-between",
        width: "100%"
    },
    content: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.default,
        padding: theme.spacing(3)
    },
    nested: {
        paddingLeft: theme.spacing(8)
    },
    menuItem: {
        paddingLeft: theme.spacing(4)
    }
}));

function InsideListItem(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const handleClick = () => {
        setOpen(!open);
    };
    return (
        <React.Fragment>
            <ListItem className={classes.menuItem} button onClick={handleClick}>
                <ListItemText primary={props.object.text}/>
                {open ? <ExpandLess/> : <ExpandMore/>}
            </ListItem>
            <Collapse in={open} timeout="auto" unmountOnExit>
                <List component="div" disablePadding>
                    {props.object.children.map((item) => (
                        <ListItem button className={classes.nested}>
                            <ListItemText primary={item.text} onClick={() => {
                                props.history.push({
                                    pathname: item.routePath,
                                    state: {}
                                })
                            }}/>
                        </ListItem>
                    ))}
                </List>
            </Collapse>
        </React.Fragment>
    );
}

function setJsonByAuth(){
    const doctorListJson = [
        {
            text: "",
            children: [
                {
                    text: "",
                    routePath: "/main/admin/patient_info"
                }
            ]
        },
        {
            text: "",
            children: [
                {
                    text: "",
                    routePath: "/main/order/drug"
                },
                {
                    text: "",
                    routePath: "/main/order/equipment"
                }
            ]
        }
    ]
    const adminListJson = [
        {
            text: "",
            children: [
                {
                    text: "",
                    routePath: "/main/admin/new_admission"
                },
                {
                    text: "",
                    routePath: "/main/admin/admission"
                },
                {
                    text: "",
                    routePath: "/main/admin/discharge"
                },
                {
                    text: "",
                    routePath: "/main/admin/transfer"
                },
                {
                    text: "",
                    routePath: "/main/admin/patient_info"
                }
            ]
        },
        {
            text: "",
            children: [
                {
                    text: "",
                    routePath: "/main/finance/recharge"
                },
                {
                    text: "",
                    routePath: "/main/finance/recourses_schedule"
                },
                {
                    text: "",
                    routePath: "/main/finance/drug_return"
                }
            ]
        },
        {
            text: "",
            children: [
                {
                    text: "",
                    routePath: "/main/stock/drug"
                },
                {
                    text: "",
                    routePath: "/main/stock/equipment"
                },
                {
                    text: "",
                    routePath: "/main/stock/sickbed"
                },
                {
                    text: "",
                    routePath: "/main/stock/staff"
                }
            ]
        }
    ]
    let auth = Cookies.get("staff_auth")
    if(auth == 1){
        return adminListJson;
    } else {
        return doctorListJson;
    }
}

function MainView(props) {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <CssBaseline/>
            <AppBar position="fixed" className={classes.appBar}>
                <Toolbar className={classes.actionBar}>
                    <Typography variant="h5" noWrap>
                        Hospital
                    </Typography>
                    <Button style={{borderColor: "white", color: "white"}} variant="outlined" onClick={() => {
                        Cookies.set("department", "")
                        Cookies.set("staff_auth", "")
                        props.history.push({
                            pathname: "/"
                        })
                    }}>
                        
                    </Button>
                </Toolbar>
            </AppBar>
            <Drawer
                className={classes.drawer}
                variant="permanent"
                classes={{
                    paper: classes.drawerPaper
                }}
                anchor="left"
            >
                <div className={classes.toolbar}/>
                <Divider/>
                <List>
                    {setJsonByAuth().map((object, index) =>
                        object.children ? (
                            <InsideListItem object={object} history={props.history}/>
                        ) : (
                            <ListItem className={classes.menuItem} button key={object.text}>
                                <ListItemText primary={object.text}/>
                            </ListItem>
                        )
                    )}
                </List>
            </Drawer>
            <main className={classes.content}>
                <div className={classes.toolbar}/>
                {props.children}
            </main>
        </div>
    );
}

export default class Main extends React.Component {
    render() {
        return (
            <MainView
                children={this.props.children}
                history={this.props.router}/>
        );
    }
}

Main.contextType = {
    router: PropTypes.object.isRequired
};