import 'date-fns';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {Button, Divider, Paper} from "@material-ui/core";
import TableContainer from "@material-ui/core/TableContainer";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogActions from "@material-ui/core/DialogActions";
import Dialog from "@material-ui/core/Dialog";

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
    table: {
        minWidth: 650,
    },
}));

function createData(equipmentId, name, location, value) {
    return {equipmentId, name, location, value};
}

function addToCurrentList(equipmentId, name, location, value, equipmentNum) {
    return {equipmentId, name, location, value, equipmentNum};
}

function EquipmentOrderView(props) {
    const classes = useStyles();
    const [equipmentName, setEquipmentName] = React.useState(null);
    const [equipmentId, setEquipmentId] = React.useState(null);
    const [equipmentNum, setEquipmentNum] = React.useState(null);
    const [medicalRecordId, setMedicalRecordId] = React.useState(null);
    const currentRows = props.currentData;
    const listRows = props.data;
    const [open, setOpen] = React.useState(false);
    const handleClickOpen = (equipmentId) => {
        setOpen(true);
        setEquipmentId(equipmentId)
    };
    const handlePositiveClose = () => {
        setOpen(false);
        props.onAdd(equipmentId, parseInt(equipmentNum))
    }
    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <React.Fragment>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Divider/>
                <Grid container>
                    <Grid item xs={6} sm={6} className={classes.formControl}>
                        <TextField required id="medical_record_id" label=""
                                   value={medicalRecordId}
                                   onChange={event => setMedicalRecordId(event.target.value)}/>
                    </Grid>
                </Grid>
                <Button variant="contained" color="primary" style={{marginTop: "0.5em"}} disableElevation
                        onClick={() => props.onComplete(medicalRecordId)}>
                    
                </Button>
                <Divider style={{marginTop: "1em"}}/>
                {/**/}
                <TableContainer component={Paper}>
                    <Table className={classes.table} aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell>ID</TableCell>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell/>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {currentRows.map((row) => (
                                <TableRow key={row.equipmentId}>
                                    <TableCell component="th" scope="row">
                                        {row.equipmentId}
                                    </TableCell>
                                    <TableCell>{row.name}</TableCell>
                                    <TableCell>{row.location}</TableCell>
                                    <TableCell>{row.value}</TableCell>
                                    <TableCell>{row.equipmentNum}</TableCell>
                                    <TableCell align={"right"}>
                                        <Button variant="contained"
                                                style={{backgroundColor: "red", borderColor: "red", color: "white"}}
                                                disableElevation
                                                onClick={() => props.onDelete(row.equipmentId)}
                                        ></Button>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
                <Divider style={{marginTop: "1em"}}/>
                <Grid container className={classes.div_inline}>
                    <Grid className={classes.formControl}>
                        <TextField required id="equipment_name" label=""
                                   value={equipmentName}
                                   onChange={event => setEquipmentName(event.target.value)}/>
                    </Grid>
                </Grid>
                <Button variant="contained" color="primary" style={{marginTop: "0.5em"}} disableElevation
                        onClick={() => props.onClick(equipmentName)}>
                    
                </Button>
            </React.Fragment>
            <Divider style={{marginTop: "1em"}}/>
            {/**/}
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>ID</TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell/>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {listRows.map((row) => (
                            row.name.indexOf(props.keyword) !== -1 ?
                                <TableRow key={row.equipmentId}>
                                    <TableCell component="th" scope="row">
                                        {row.equipmentId}
                                    </TableCell>
                                    <TableCell>{row.name}</TableCell>
                                    <TableCell>{row.location}</TableCell>
                                    <TableCell>{row.value}</TableCell>
                                    <TableCell align={"right"}>
                                        <Button variant="contained"
                                                style={{backgroundColor: "green", borderColor: "green", color: "white"}}
                                                disableElevation
                                                onClick={() => handleClickOpen(row.equipmentId)}
                                        ></Button>
                                    </TableCell>
                                </TableRow> : null
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>

            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title"></DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        :
                    </DialogContentText>
                    <TextField
                        autoFocus
                        fullWidth
                        id="equipmentNum"
                        value={equipmentNum}
                        onChange={event => setEquipmentNum(event.target.value)}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handlePositiveClose} color="primary">
                        
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    )
}

export default class EquipmentOrder extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            currentData: [],
            keyword: ""
        }
    }

    componentDidMount() {
        fetch("/info/equipment?equipment_name=", {
            method: "GET",
        })
            .then(res => res.json())
            .then(res => {
                if (res.code === 0) {
                    let data = JSON.parse(res.data.equipment_info)
                    let newData = [];
                    for (let json of data) {
                        newData.push(createData(json.equipment_id, json.equipment_name, json.equipment_location, json.value / 100))
                    }
                    this.setState({
                        data: newData
                    });
                } else {
                    alert(res.msg)
                }
            }, error => {
                alert("")
            })
    }

    render() {
        return <EquipmentOrderView
            onClick={(equipmentName) => {
                this.setState({
                    keyword: equipmentName
                })
            }}
            onAdd={(equipmentId, equipmentNum) => {
                if (equipmentNum < 0) {
                    alert("")
                } else {
                    let newCurrentData = this.state.currentData;
                    let data = this.state.data;
                    if (newCurrentData.length > 0) {
                        alert("")
                    } else {
                        let flag = false;
                        for (let i = 0; i < data.length; i++) {
                            let dataRow = data[i]
                            if (equipmentId === dataRow.equipmentId) {
                                console.log(addToCurrentList(dataRow.equipmentId, dataRow.name, dataRow.location, dataRow.value, equipmentNum));
                                newCurrentData.push(addToCurrentList(dataRow.equipmentId, dataRow.name, dataRow.location, dataRow.value, equipmentNum));
                                console.log(newCurrentData)
                                flag = true;
                                break;
                            }
                        }
                        if (!flag) {
                            alert("");
                        }
                        this.setState({
                            data: data,
                            currentData: newCurrentData
                        })
                    }
                }
            }}
            onDelete={(equipmentId) => {
                let newCurrentData = this.state.currentData;
                let newData = this.state.data;
                for (let i = 0; i < newCurrentData.length; i++) {
                    if (newCurrentData[i].equipmentId === equipmentId) {
                        newCurrentData.splice(i, 1);
                    }
                }
                this.setState({
                    currentData: newCurrentData,
                    data: newData
                })
            }}
            onComplete={(medicalRecordId) => {
                let data = new FormData();
                data.append("medical_record_id", medicalRecordId);
                data.append("equipment_id", this.state.currentData[0].equipmentId);
                data.append("equipment_num", this.state.currentData[0].equipmentNum);
                fetch("/order/equipment_order", {
                    method: "POST",
                    body: data
                })
                    .then(res => res.json())
                    .then(res => {
                        if (res.code === 0) {
                            alert(res.msg)
                            window.location.reload()
                        } else {
                            alert(res.msg)
                        }
                    }, error => {
                        alert("")
                    })
            }}
            data={this.state.data}
            currentData={this.state.currentData}
            keyword={this.state.keyword}
        />
    }
}
