import 'date-fns';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {Button, Divider, Paper} from "@material-ui/core";
import TableContainer from "@material-ui/core/TableContainer";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import Cookies from 'js-cookie'

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
    table: {
        minWidth: 650,
    },
}));

function createData(medical_record_id, name, sex, department, room, balance) {
    return {medical_record_id, name, sex, department, room, balance};
}

function PatientInfoView(props) {
    const classes = useStyles();
    const [medicalRecordId, setMedicalRecordId] = React.useState(null);
    const rows = props.data;

    return (
        <div>
            <React.Fragment>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Divider/>
                <Grid container className={classes.div_inline}>
                    <Grid className={classes.formControl}>
                        <TextField required id="medical_record_id" label=""
                                   value={medicalRecordId}
                                   onChange={event => setMedicalRecordId(event.target.value)}/>
                    </Grid>
                </Grid>
                <Button variant="contained" color="primary" style={{marginTop: "0.5em"}} disableElevation
                        onClick={() => props.onClick(medicalRecordId)}>
                    
                </Button>
            </React.Fragment>
            <Divider style={{marginTop: "1em"}}/>
            {/**/}
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            <TableRow key={row.medical_record_id}>
                                <TableCell component="th" scope="row">{row.medical_record_id}</TableCell>
                                <TableCell>{row.name}</TableCell>
                                <TableCell>{row.sex}</TableCell>
                                <TableCell>{row.department}</TableCell>
                                <TableCell>{row.room}</TableCell>
                                <TableCell>{row.balance}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    );
}

export default class PatientInfo extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: []
        }
    }

    componentDidMount() {
        fetch("/info/patient?department=" + Cookies.get("department"), {
            method: "GET",
        })
            .then(res => res.json())
            .then(res => {
                if (res.code === 0) {
                    let data = JSON.parse(res.data.patient_info)
                    let newData = [];
                    for(let json of data){
                        console.log(json)
                        newData.push(createData(json.medical_record_id, json.name, json.sex, json.current_department, json.room_id, json.balance/100));
                    }
                    this.setState({
                        data: newData
                    });
                } else {
                    alert(res.msg)
                }
            }, error => {
                alert("")
            })
    }

    render() {
        return <PatientInfoView
            onClick={(medicalRecordId) => {
                fetch("/info/patient_info?medical_record_id=" + medicalRecordId, {
                    method: "GET",
                })
                    .then(res => res.json())
                    .then(res => {
                        if (res.code === 0) {
                            let data = JSON.parse(res.data.patient_info)
                            let newData = [
                                createData(data.medical_record_id, data.name, data.sex, data.current_department, data.room_id, data.balance / 100),
                            ];
                            this.setState({
                                data: newData
                            });
                        } else {
                            alert(res.msg)
                        }
                    }, error => {
                        alert("")
                    })
            }}
            data={this.state.data}
        />
    }
}
