import 'date-fns';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {Button, Divider, Paper} from "@material-ui/core";
import TableContainer from "@material-ui/core/TableContainer";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {PropTypes} from "prop-types";
import {render} from "@testing-library/react";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContentText from "@material-ui/core/DialogContentText";

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
    table: {
        minWidth: 650,
    },
}));

function createData(drugId, drugCode, expirationDate, value, drugName, specification, company, form, stock) {
    return {drugId, drugCode, expirationDate, value, drugName, specification, company, form, stock};
}

function DrugStockView(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [countOpen, setCountOpen] = React.useState(false);
    const rows = props.data;
    const [drugSelectName, setDrugSelectName] = React.useState(null);
    const [drugCode, setDrugCode] = React.useState(null);
    const [expirationDate, setExpirationDate] = React.useState(null);
    const [value, setValue] = React.useState(null);
    const [drugName, setDrugName] = React.useState(null);
    const [specification, setSpecification] = React.useState(null);
    const [company, setCompany] = React.useState(null);
    const [form, setForm] = React.useState(null);
    const [stock, setStock] = React.useState(null);
    const [drugIdSet, setDrugIdSet] = React.useState(null);
    const [stockSet, setStockSet] = React.useState(null);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleCountOpen = (drugId) => {
        setCountOpen(true);
        setDrugIdSet(drugId)
    };

    const handleCountClose = () => {
        setCountOpen(false);
    };

    return (
        <div>
            <React.Fragment>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Divider/>
                <Grid container className={classes.div_inline}>
                    <Grid className={classes.formControl}>
                        <TextField required id="drug_name" label=""
                                   value={drugSelectName}
                                   onChange={event => setDrugSelectName(event.target.value)}/>
                    </Grid>
                </Grid>
                <Button variant="contained" color="primary" style={{marginTop: "0.5em"}} disableElevation
                        onClick={() => props.onClickSelect(drugSelectName)}>
                    
                </Button>
                <Button variant="contained" style={{
                    marginLeft: "0.5em",
                    marginTop: "0.5em",
                    broaderColor: "green",
                    backgroundColor: "green",
                    color: "white"
                }}
                        onClick={handleClickOpen}
                        disableElevation>
                    
                </Button>
                {/**/}
                <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title"></DialogTitle>
                    <DialogContent>
                        <TextField
                            autoFocus
                            margin="dense"
                            id="drug_code"
                            value={drugCode}
                            onChange={event => setDrugCode(event.target.value)}
                            label=""
                            fullWidth
                        />
                        <TextField
                            autoFocus
                            margin="dense"
                            id="expiration_date"
                            value={expirationDate}
                            onChange={event => setExpirationDate(event.target.value)}
                            label=""
                            fullWidth
                        />
                        <TextField
                            autoFocus
                            margin="dense"
                            id="value"
                            value={value}
                            onChange={event => setValue(event.target.value)}
                            label=""
                            fullWidth
                        />
                        <TextField
                            autoFocus
                            margin="dense"
                            id="drug_name"
                            value={drugName}
                            onChange={event => setDrugName(event.target.value)}
                            label=""
                            fullWidth
                        />
                        <TextField
                            autoFocus
                            margin="dense"
                            id="specification"
                            value={specification}
                            onChange={event => setSpecification(event.target.value)}
                            label=""
                            fullWidth
                        />
                        <TextField
                            autoFocus
                            margin="dense"
                            id="company"
                            value={company}
                            onChange={event => setCompany(event.target.value)}
                            label=""
                            fullWidth
                        />
                        <TextField
                            autoFocus
                            margin="dense"
                            id="form"
                            value={form}
                            onChange={event => setForm(event.target.value)}
                            label=""
                            fullWidth
                        />
                        <TextField
                            autoFocus
                            margin="dense"
                            id="stock"
                            value={stock}
                            onChange={event => setStock(event.target.value)}
                            label=""
                            fullWidth
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleClose} color="primary">
                            
                        </Button>
                        <Button
                            onClick={() => props.onClickAdd(drugCode, expirationDate, value*100, drugName, specification, company, form, stock)}
                            color="primary">
                            
                        </Button>
                    </DialogActions>
                </Dialog>

            </React.Fragment>
            <Divider style={{marginTop: "1em"}}/>
            {/**/}
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>ID</TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell/>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            row.drugName.indexOf(props.keyword) !== -1 ?
                                <TableRow key={row.drugId}>
                                    <TableCell component="th" scope="row">
                                        {row.drugId}
                                    </TableCell>
                                    <TableCell>{row.drugCode}</TableCell>
                                    <TableCell>{row.expirationDate}</TableCell>
                                    <TableCell>{row.value}</TableCell>
                                    <TableCell>{row.drugName}</TableCell>
                                    <TableCell>{row.specification}</TableCell>
                                    <TableCell>{row.company}</TableCell>
                                    <TableCell>{row.form}</TableCell>
                                    <TableCell>{row.stock}</TableCell>
                                    <TableCell align={"right"}>
                                        <Button variant="contained" color="primary" disableElevation
                                                onClick={() => handleCountOpen(row.drugId)}></Button>
                                        <Button variant="contained" style={{
                                            marginLeft: "1em",
                                            backgroundColor: "red",
                                            borderColor: "red",
                                            color: "white"
                                        }}
                                                onClick={() => props.onClickDelete(row.drugId)}
                                                disableElevation></Button>
                                    </TableCell>
                                </TableRow> : null
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>

            <Dialog open={countOpen} onClose={handleCountClose} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title"></DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        
                    </DialogContentText>
                    <TextField
                        autoFocus
                        fullWidth
                        id="stock_set"
                        value={stockSet}
                        onChange={event => setStockSet(event.target.value)}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => props.onClickSet(drugIdSet, parseInt(stockSet))} color="primary">
                        
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    )
}

export default class DrugStock extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            keyword: ""
        }
    }

    componentDidMount() {
        fetch("/info/drug?drug_name=", {
            method: "GET",
        })
            .then(res => res.json())
            .then(res => {
                if (res.code === 0) {
                    let data = JSON.parse(res.data.drug_info)
                    let newData = [];
                    for (let json of data) {
                        newData.push(createData(json.drug_id, json.drug_code, json.expiration_date, json.value / 100, json.drug_name, json.specification, json.company, json.form, json.stock))
                    }
                    this.setState({
                        data: newData
                    });
                } else {
                    alert(res.msg)
                }
            }, error => {
                alert("")
            })
    }

    render() {
        return <DrugStockView
            onClickSelect={(drugName) => {
                this.setState({
                    keyword: drugName
                })
            }}
            onClickAdd={(drugCode, expirationDate, value, drugName, specification, company, form, stock) => {
                let data = new FormData();
                data.append("drug_code", drugCode);
                data.append("expiration_date", expirationDate);
                data.append("value", value);
                data.append("drug_name", drugName);
                data.append("specification", specification);
                data.append("company", company);
                data.append("form", form);
                data.append("stock", stock);

                fetch("/finance/drug/add", {
                    method: "POST",
                    body: data
                })
                    .then(res => res.json())
                    .then(res => {
                        alert(res.msg)
                        if(res.code === 0){
                            window.location.reload()
                        }
                    }, error => {
                        alert("")
                    })
            }}
            onClickSet={(drugId, stockNum) => {
                let data = new FormData();
                data.append("drug_id", drugId);
                data.append("stock_num", stockNum);

                fetch("/finance/drug/set_stock", {
                    method: "POST",
                    body: data
                })
                    .then(res => res.json())
                    .then(res => {
                        alert(res.msg)
                        if (res.code === 0) {
                            window.location.reload()
                        }
                    }, error => {
                        alert("")
                    })
            }}
            onClickDelete={(drugId) => {
                let data = new FormData();
                data.append("drug_id", drugId);

                fetch("/finance/drug/remove", {
                    method: "POST",
                    body: data
                })
                    .then(res => res.json())
                    .then(res => {
                        alert(res.msg)
                        window.location.reload()
                    }, error => {
                        alert("")
                    })
            }}
            data={this.state.data}
            keyword={this.state.keyword}
        />
    }
}

DrugStock.contextType = {
    router: PropTypes.object.isRequired
}
