import 'date-fns';
import React from 'react';
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {Button, Divider} from "@material-ui/core";
import {PropTypes} from "prop-types";

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 240,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
    div_full: {
        display: "block",
        width: "100%"
    },
    div_inline: {
        display: "inline-block",
        width: "auto"
    },
    container: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
        width: 200,
    },
}));

function NewAdmissionView(props) {

    const classes = useStyles();
    const [idNumber, setIdNumber] = React.useState(null);
    const [medicalRecordId, setMedicalRecordId] = React.useState(null);
    const [name, setName] = React.useState(null);
    const [sex, setSex] = React.useState(null);
    const [birthday, setBirthday] = React.useState(null);

    return (
        <div>
            <React.Fragment>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Divider/>
                <div className={classes.div_inline}>
                    <TextField required id="id_number" label=""
                               value={idNumber} className={classes.formControl}
                               onChange={event => setIdNumber(event.target.value)}/>
                </div>
                <div className={classes.div_inline}>
                    <TextField required id="medical_record_id" label=""
                               value={medicalRecordId} className={classes.formControl}
                               onChange={event => setMedicalRecordId(event.target.value)}/>
                </div>
                <div/>
                <div className={classes.div_inline}>
                    <TextField required id="name" label="" className={classes.formControl}
                               value={name}
                               onChange={event => setName(event.target.value)}/>
                </div>
                <div className={classes.div_inline}>
                    <FormControl className={classes.formControl}>
                        <InputLabel id="sex"></InputLabel>
                        <Select
                            id="sex"
                            value={sex}
                            onChange={event => setSex(event.target.value)}
                        >
                            <MenuItem value={""}></MenuItem>
                            <MenuItem value={""}></MenuItem>
                        </Select>
                    </FormControl>
                </div>
                <form className={classes.container} noValidate>
                    <TextField
                        id="birthday"
                        label=""
                        type="date"
                        className={classes.textField}
                        value={birthday}
                        onChange={event => setBirthday(event.target.value)}
                        InputLabelProps={{
                            shrink: true,
                        }}
                    />
                </form>
            </React.Fragment>
            <Divider style={{marginTop: "0.5em"}}/>
            <Button variant="contained" color="primary" style={{marginTop: "1em"}} disableElevation
                    onClick={() => props.onClick(idNumber, medicalRecordId, name, sex, birthday)}
            >
            </Button>
        </div>
    );
}

export default class NewAdmission extends React.Component {
    render() {
        return <NewAdmissionView
            onClick={(idNumber, medicalRecordId, name, sex, birthday) => {
                let data = new FormData();
                data.append("id_number", idNumber);
                data.append("medical_record_id", medicalRecordId);
                data.append("name", name);
                data.append("sex", sex);
                data.append("birthday", birthday);
                fetch("/admin/admission/new", {
                    method: "POST",
                    body: data
                })
                    .then(res => res.json())
                    .then(res => {
                        if (res.code === 0) {
                            alert(res.msg)
                            window.location.reload()
                        } else {
                            alert(res.msg)
                        }
                    }, error => {
                        alert("")
                    })
            }}
        />
    }
}

NewAdmission.contextType = {
    router: PropTypes.object.isRequired
}
