import 'date-fns';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {Button, Divider, Paper} from "@material-ui/core";
import TableContainer from "@material-ui/core/TableContainer";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContentText from "@material-ui/core/DialogContentText";

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
    table: {
        minWidth: 650,
    },
}));

function createData(drugId, drugCode, expirationDate, value, drugName, specification, company, form, stock) {
    return {drugId, drugCode, expirationDate, value, drugName, specification, company, form, stock};
}

function addToCurrentList(drugId, drugCode, expirationDate, value, drugName, drugNum) {
    return {drugId, drugCode, expirationDate, value, drugName, drugNum}
}

function DrugOrderView(props) {
    const classes = useStyles();
    const [drugName, setDrugName] = React.useState(null);
    const [drugId, setDrugId] = React.useState(null);
    const [drugNum, setDrugNum] = React.useState(null);
    const [medicalRecordId, setMedicalRecordId] = React.useState(null);
    const currentRows = props.currentData;
    const listRows = props.data;
    const [open, setOpen] = React.useState(false);
    const handleClickOpen = (drugId) => {
        setOpen(true);
        setDrugId(drugId);
    };
    const handlePositiveClose = () => {
        setOpen(false);
        //
        props.onAdd(drugId, parseInt(drugNum));
    }
    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <React.Fragment>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Divider/>
                <Grid container>
                    <Grid item xs={6} sm={6} className={classes.formControl}>
                        <TextField required id="medical_record_id" label=""
                                   value={medicalRecordId}
                                   onChange={event => setMedicalRecordId(event.target.value)}/>
                    </Grid>
                </Grid>
                <Button variant="contained" color="primary" style={{marginTop: "0.5em"}} disableElevation
                        onClick={() => props.onComplete(medicalRecordId)}>
                    
                </Button>
                <Divider style={{marginTop: "1em"}}/>
                {/**/}
                <TableContainer component={Paper}>
                    <Table className={classes.table} aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                <TableCell>ID</TableCell>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell/>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {currentRows.map((row) => (
                                <TableRow key={row.drugId}>
                                    <TableCell component="th" scope="row">
                                        {row.drugId}
                                    </TableCell>
                                    <TableCell>{row.drugCode}</TableCell>
                                    <TableCell>{row.expirationDate}</TableCell>
                                    <TableCell>{row.value}</TableCell>
                                    <TableCell>{row.drugName}</TableCell>
                                    <TableCell>{row.drugNum}</TableCell>
                                    <TableCell align={"right"}>
                                        <Button variant="contained"
                                                style={{backgroundColor: "red", borderColor: "red", color: "white"}}
                                                disableElevation
                                                onClick={() => props.onDelete(row.drugId)}
                                        ></Button>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
                <Divider style={{marginTop: "1em"}}/>
                <Grid container className={classes.div_inline}>
                    <Grid className={classes.formControl}>
                        <TextField required id="drug_name" label=""
                                   value={drugName}
                                   onChange={event => setDrugName(event.target.value)}/>
                    </Grid>
                </Grid>
                <Button variant="contained" color="primary" style={{marginTop: "0.5em"}} disableElevation
                        onClick={() => props.onClick(drugName)}>
                    
                </Button>
            </React.Fragment>
            <Divider style={{marginTop: "1em"}}/>
            {/**/}
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>ID</TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell></TableCell>
                            <TableCell/>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {listRows.map((row) => (
                            row.drugName.indexOf(props.keyword) !== -1 ?
                                <TableRow key={row.drugId}>
                                    <TableCell component="th" scope="row">
                                        {row.drugId}
                                    </TableCell>
                                    <TableCell>{row.drugCode}</TableCell>
                                    <TableCell>{row.expirationDate}</TableCell>
                                    <TableCell>{row.value}</TableCell>
                                    <TableCell>{row.drugName}</TableCell>
                                    <TableCell>{row.specification}</TableCell>
                                    <TableCell>{row.company}</TableCell>
                                    <TableCell>{row.form}</TableCell>
                                    <TableCell>{row.stock}</TableCell>
                                    <TableCell align={"right"}>
                                        <Button variant="contained"
                                                style={{backgroundColor: "green", borderColor: "green", color: "white"}}
                                                disableElevation
                                                onClick={() => handleClickOpen(row.drugId)}
                                        ></Button>
                                    </TableCell>
                                </TableRow> : null
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>

            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
                <DialogTitle id="form-dialog-title"></DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        :
                    </DialogContentText>
                    <TextField
                        autoFocus
                        fullWidth
                        id="drugNum"
                        value={drugNum}
                        onChange={event => setDrugNum(event.target.value)}
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={handlePositiveClose} color="primary">
                        
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    )
}

export default class DrugOrder extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            currentData: [],
            keyword: ""
        }
    }

    componentDidMount() {
        fetch("/info/drug?drug_name=", {
            method: "GET",
        })
            .then(res => res.json())
            .then(res => {
                if (res.code === 0) {
                    let data = JSON.parse(res.data.drug_info)
                    let newData = [];
                    for (let json of data) {
                        newData.push(createData(json.drug_id, json.drug_code, json.expiration_date, json.value / 100, json.drug_name, json.specification, json.company, json.form, json.stock))
                    }
                    this.setState({
                        data: newData
                    });
                } else {
                    alert(res.msg)
                }
            }, error => {
                alert("")
            })
    }

    render() {
        return <DrugOrderView
            onClick={(drugName) => {
                this.setState({
                    keyword: drugName
                })
            }}
            onAdd={(drugId, drugNum) => {
                if (drugNum < 0) {
                    alert("")
                } else {
                    let newCurrentData = this.state.currentData;
                    let data = this.state.data;
                    let flag = false;
                    let existFlag = false;
                    for (let i = 0; i < data.length; i++) {
                        let dataRow = data[i];
                        if (drugId === dataRow.drugId && drugNum <= dataRow.stock) {
                            data[i].stock -= drugNum;
                            for (let m = 0; m < newCurrentData.length; m++) {
                                if (drugId === newCurrentData[m].drugId) {
                                    newCurrentData[m].drugNum += drugNum;
                                    existFlag = true;
                                    break;
                                }
                            }
                            if (!existFlag) {
                                newCurrentData.push(addToCurrentList(dataRow.drugId, dataRow.drugCode, dataRow.expirationDate, dataRow.value, dataRow.drugName, drugNum));
                            }
                            flag = true;
                        }
                    }
                    if (!flag) {
                        alert("");
                    }
                    this.setState({
                        data: data,
                        currentData: newCurrentData
                    })
                }
            }}
            onDelete={(drugId) => {
                let newCurrentData = this.state.currentData;
                let newData = this.state.data;
                for (let i = 0; i < newCurrentData.length; i++) {
                    if (newCurrentData[i].drugId === drugId) {
                        for (let m = 0; m < newData.length; m++) {
                            if (newData[m].drugId === drugId) {
                                newData[m].stock += parseInt(newCurrentData[i].drugNum)
                            }
                        }
                        newCurrentData.splice(i, 1);
                    }
                }
                this.setState({
                    currentData: newCurrentData,
                    data: newData
                })
            }}
            onComplete={(medicalRecordId) => {
                let drugList = [];
                for (let i = 0; i < this.state.currentData.length; i++) {
                    let drugOne = {
                        drug_id: this.state.currentData[i].drugId,
                        drug_count: this.state.currentData[i].drugNum
                    }
                    drugList.push(drugOne)
                }
                let data = new FormData();
                data.append("medical_record_id", medicalRecordId);
                data.append("drug_list", JSON.stringify(drugList));
                fetch("/order/drug_order", {
                    method: "POST",
                    body: data
                })
                    .then(res => res.json())
                    .then(res => {
                        if (res.code === 0) {
                            alert(res.msg)
                            window.location.reload()
                        } else {
                            alert(res.msg)
                        }
                    }, error => {
                        alert("")
                    })
            }}
            data={this.state.data}
            currentData={this.state.currentData}
            keyword={this.state.keyword}
        />
    }

}
