import 'date-fns';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import makeStyles from "@material-ui/core/styles/makeStyles";
import {Button, Divider, Paper} from "@material-ui/core";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import TableContainer from "@material-ui/core/TableContainer";
import {PropTypes} from "prop-types";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContentText from "@material-ui/core/DialogContentText";

const useStyles = makeStyles((theme) => ({
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    selectEmpty: {
        marginTop: theme.spacing(2),
    },
    table: {
        minWidth: 650,
    },
}));

function createData(orderId, countSum) {
    return {orderId, countSum};
}

function RecoursesScheduleView(props) {
    const classes = useStyles();
    const [medicalRecordId, setMedicalRecordId] = React.useState(null);
    const [orderId, setOrderId] = React.useState(null);
    const rows = props.data;
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = (orderId) => {
        setOpen(true);
        props.onDetail(orderId)
    };

    const handleClose = () => {
        setOpen(false);
    };
    return (
        <div>
            <React.Fragment>
                <Typography variant="h6" gutterBottom>
                    
                </Typography>
                <Divider/>
                <Grid container>
                    <Grid item xs={6} sm={6} className={classes.formControl}>
                        <TextField required id="medical_record_id" label=""
                                   value={medicalRecordId}
                                   onChange={event => setMedicalRecordId(event.target.value)}/>
                    </Grid>
                </Grid>
            </React.Fragment>

            <Button variant="contained" color="primary" style={{marginTop: "0.5em"}} disableElevation
                    onClick={() => props.onClick(medicalRecordId)}
            >
            </Button>
            <Divider style={{marginTop: "1em"}}/>
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>ID</TableCell>
                            <TableCell></TableCell>
                            <TableCell/>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            <TableRow key={row.orderId}>
                                <TableCell component="th" scope="row">
                                    {row.orderId}
                                </TableCell>
                                <TableCell>{row.countSum}</TableCell>
                                <TableCell align={"right"}>
                                    <Button variant="contained" color="primary" disableElevation
                                            onClick={() => handleClickOpen(row.orderId)}
                                    ></Button>
                                    <Button variant="contained" color="primary" style={{marginLeft: "1em"}}
                                            disableElevation
                                            onClick={() => props.onComplete(row.orderId)}></Button>
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>

            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">{""}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        {props.detail}
                    </DialogContentText>
                </DialogContent>
            </Dialog>
        </div>
    );
}

export default class RecoursesSchedule extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: [],
            detail: ""
        }
    }

    render() {
        return <RecoursesScheduleView
            onClick={(medicalRecordId) => {
                fetch("/info/order?medical_record_id=" + medicalRecordId, {
                    method: "GET",
                })
                    .then(res => res.json())
                    .then(res => {
                        if (res.code === 0) {
                            let data = JSON.parse(res.data.order_info)
                            let newData = [];
                            for (let json of data) {
                                newData.push(createData(json.orderId, json.sum / 100))
                            }
                            this.setState({
                                data: newData
                            });
                        } else {
                            alert(res.msg)
                        }
                    }, error => {
                        alert("")
                    })
            }}
            onDetail={(orderId) => {
                let data = new FormData();
                data.append("order_id", orderId);
                fetch("/finance/order/detail", {
                    method: "POST",
                    body: data
                })
                    .then(res => res.json())
                    .then(res => {
                        let detail = JSON.parse(res.data.data)
                        let newDetail = "";
                        for (let i = 0; i < detail.length; i++) {
                            newDetail = newDetail + detail[i].resources_name + "*" + detail[i].resources_num + "," + detail[i].resources_value / 100 * parseInt(detail[i].resources_num) + ",";
                        }
                        if (res.code === 0) {
                            this.setState({
                                detail: newDetail
                            })
                        } else {
                            alert(res.msg)
                        }
                    }, error => {
                        alert("")
                    })
            }}
            onComplete={(orderId) => {
                let data = new FormData();
                data.append("order_id", orderId);
                fetch("/finance/resources/schedule", {
                    method: "POST",
                    body: data
                })
                    .then(res => res.json())
                    .then(res => {
                        alert(res.msg)
                        if(res.code === 0){
                            window.location.reload()
                        }
                    }, error => {
                        alert("")
                    })
            }}
            data={this.state.data}
            detail={this.state.detail}
        />
    }
}

RecoursesSchedule.contextType = {
    router: PropTypes.object.isRequired
}
